.page-header__wrapper {
  margin: 0 auto;
  padding-left: 48px;
  padding-right: 45px;
  display: flex;
  justify-content: space-between;
  max-width: 1440px; }

.page-header__logo {
  margin-right: 48px; }

.page-header__phone-container {
  padding-top: 41px;
  min-width: 140px; }

.page-header__phone-number {
  margin: 0;
  color: #ffffff;
  font-size: 14px;
  font-weight: 600;
  line-height: 17px;
  font-family: "Montserrat", "Open Sans", sans-serif;
  text-transform: uppercase;
  text-decoration: none; }

.page-header__toggle {
  display: none; }

@media (max-width: 1023px) {
  .page-header__wrapper {
    padding-top: 17px;
    padding-right: 28px;
    padding-bottom: 16px;
    padding-left: 20px; }
  .page-header__logo {
    margin: 0;
    margin-right: auto; }
  .page-header__phone-container {
    padding-top: 7px;
    padding-right: 100px;
    width: 100%;
    min-width: 140px;
    text-align: center; }
  .page-header__toggle {
    position: relative;
    margin-top: 5px;
    display: block;
    width: 34px;
    height: 18px;
    background: none;
    border: none;
    cursor: pointer; }
    .page-header__toggle::before {
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 34px;
      height: 2px;
      background-color: #ffffff;
      box-shadow: 0 8px 0 0 #ffffff;
      content: ""; }
    .page-header__toggle::after {
      position: absolute;
      bottom: 0;
      left: 0;
      display: block;
      width: 34px;
      height: 2px;
      background-color: #ffffff;
      content: ""; }
  .page-header--opened {
    display: flex;
    flex-direction: column;
    flex: auto;
    min-height: 100vh;
    background-color: rgba(4, 4, 4, 0.9); }
  .page-header--opened .page-header__wrapper {
    padding-right: 23px;
    flex-wrap: wrap;
    background-color: transparent; }
    .page-header--opened .page-header__wrapper::before {
      position: absolute;
      left: 0;
      top: 223px;
      display: block;
      width: 100%;
      height: 2px;
      background-color: #ffffff;
      opacity: 0.16;
      content: ""; }
  .page-header--opened .page-header__logo {
    margin-bottom: 108px; }
  .page-header--opened .page-header__phone-container {
    position: relative;
    padding: 0;
    padding-right: 8px;
    text-align: center;
    order: 1; }
  .page-header--opened .page-header__phone-number {
    font-size: 32px;
    line-height: 125%;
    font-weight: 700;
    text-align: center; }
  .page-header--opened .page-header__toggle {
    margin-top: 14px; }
    .page-header--opened .page-header__toggle::before {
      transform: rotate(45deg);
      box-shadow: none; }
    .page-header--opened .page-header__toggle::after {
      top: 0;
      bottom: auto;
      transform: rotate(-45deg); } }

@media (max-width: 767px) {
  .page-header__wrapper {
    padding-right: 23px;
    min-width: 320px; }
  .page-header--opened .page-header__wrapper::before {
    top: 123px; }
  .page-header--opened .page-header__logo {
    margin-bottom: 30px; }
  .page-header__phone-container {
    display: none; }
  .page-header--opened .page-header__phone-container {
    padding: 0;
    display: block; }
  .page-header--opened .page-header__phone-number {
    font-size: 20px; }
  .page-header__toggle {
    position: relative;
    margin-top: 6px; } }

